/* Reset và cài đặt chung */
:root {
  --primary: #7C4DFF;
  --primary-light: #9D7FFF;
  --primary-dark: #5E35B1;
  --secondary: #FF4081;
  --secondary-light: #FF80AB;
  --secondary-dark: #F50057;
  --success: #00C853;
  --warning: #FFD600;
  --danger: #FF1744;
  --background-dark: #0A0A0A;
  --background-dark-light: #121212;
  --background-dark-lighter: #1E1E1E;
  --card-bg: #1A1A1A;
  --text-light: #FFFFFF;
  --text-light-muted: #B0B0B0;
  --border-dark: #2D2D2D;
  --shadow-dark: 0 8px 16px rgba(0, 0, 0, 0.5);
  --shadow-soft: 0 4px 20px rgba(0, 0, 0, 0.25);
  --transition-speed: 0.3s;
  --border-radius: 12px;
  --border-radius-sm: 8px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-dark);
  color: var(--text-light);
  line-height: 1.6;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  padding: 2rem 0;
}

a {
  color: var(--primary);
  text-decoration: none;
  transition: color var(--transition-speed);
}

a:hover {
  color: var(--primary-light);
}

/* Header & Navigation */
header {
  background-color: rgba(10, 10, 10, 0.95);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: var(--shadow-soft);
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.brand {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
  margin-right: 2rem;
  position: relative;
  display: inline-block;
}

.brand::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--secondary);
  transition: width 0.3s ease;
}

.brand:hover::after {
  width: 100%;
}

.nav-links {
  display: flex;
  list-style: none;
  align-items: center;
}

.nav-links li {
  margin: 0 1rem;
}

.nav-links a, .nav-links button {
  color: var(--text-light);
  font-weight: 500;
  transition: all var(--transition-speed);
  position: relative;
  padding: 0.5rem 0;
}

.nav-links a::after, .nav-links button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--primary);
  transition: width 0.3s ease;
}

.nav-links a:hover::after, .nav-links button:hover::after {
  width: 100%;
}

.nav-links a:hover, .nav-links button:hover {
  color: var(--primary);
}

/* Home Page */
.home-header {
  margin-bottom: 3rem;
  text-align: center;
}

.home-title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.home-subtitle {
  color: var(--text-light-muted);
  font-size: 1.1rem;
  max-width: 700px;
  margin: 0 auto;
}

.post-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.post-card {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow-soft);
  transition: all var(--transition-speed);
  border: 1px solid var(--border-dark);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.post-card:hover {
  transform: translateY(-6px);
  box-shadow: var(--shadow-dark);
  border-color: rgba(124, 77, 255, 0.3);
}

.post-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.post-card-content {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.post-card h2 {
  font-size: 1.25rem;
  margin-bottom: 0.8rem;
  line-height: 1.4;
}

.post-card-excerpt {
  color: var(--text-light-muted);
  margin-bottom: 1rem;
  flex-grow: 1;
}

.post-card-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem;
  color: var(--text-light-muted);
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-dark);
}

.post-card-meta span {
  display: flex;
  align-items: center;
}

.post-card-meta svg {
  margin-right: 5px;
}

.post-card-actions {
  margin-top: 1.5rem;
}

/* Post Detail */
.post-detail {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2.5rem;
  margin-bottom: 2.5rem;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.post-header {
  margin-bottom: 2rem;
}

.post-title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  line-height: 1.3;
}

.post-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--text-light-muted);
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid var(--border-dark);
}

.post-meta-author {
  display: flex;
  align-items: center;
}

.author-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.post-meta-info {
  display: flex;
  gap: 1rem;
}

.post-meta-info span {
  display: flex;
  align-items: center;
}

.post-meta-info svg {
  margin-right: 5px;
}

.post-image {
  width: 100%;
  max-height: 500px;
  object-fit: cover;
  border-radius: var(--border-radius-sm);
  margin-bottom: 2rem;
}

.post-content {
  line-height: 1.8;
  margin-bottom: 2.5rem;
  font-size: 1.05rem;
}

.post-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 2rem;
}

.post-tag {
  background-color: rgba(124, 77, 255, 0.1);
  color: var(--primary-light);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
}

/* Comments */
.comment-section {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2rem;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.comment-section-title {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border-dark);
}

.comment-form {
  margin-bottom: 2.5rem;
}

.comment-list {
  margin-top: 2rem;
}

.comment {
  background-color: var(--background-dark-lighter);
  border-radius: var(--border-radius-sm);
  padding: 1.2rem;
  margin-bottom: 1.2rem;
}

.comment-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.8rem;
}

.comment-author {
  font-weight: 600;
  display: flex;
  align-items: center;
}

.comment-author-avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: bold;
}

.comment-date {
  color: var(--text-light-muted);
  font-size: 0.85rem;
}

.comment-content {
  line-height: 1.6;
}

/* Forms */
.form-container {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2.5rem;
  max-width: 600px;
  margin: 0 auto;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.form-title {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
}

.form-group {
  margin-bottom: 1.8rem;
}

label {
  display: block;
  margin-bottom: 0.6rem;
  font-weight: 500;
}

input, textarea, select {
  width: 100%;
  padding: 0.9rem 1.2rem;
  border: 1px solid var(--border-dark);
  border-radius: var(--border-radius-sm);
  background-color: var(--background-dark-lighter);
  color: var(--text-light);
  transition: all var(--transition-speed);
  font-family: inherit;
  font-size: 1rem;
}

input:focus, textarea:focus, select:focus {
  border-color: var(--primary);
  outline: none;
  box-shadow: 0 0 0 3px rgba(124, 77, 255, 0.2);
}

textarea {
  min-height: 150px;
  resize: vertical;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.8rem 1.6rem;
  border-radius: var(--border-radius-sm);
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  transition: all var(--transition-speed);
  border: none;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
}

.btn svg {
  margin-right: 8px;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  box-shadow: 0 4px 10px rgba(124, 77, 255, 0.3);
}

.btn-primary:hover {
  background: linear-gradient(135deg, var(--primary-light), var(--primary));
  box-shadow: 0 6px 15px rgba(124, 77, 255, 0.4);
  transform: translateY(-2px);
}

.btn-secondary {
  background-color: transparent;
  color: var(--text-light);
  border: 1px solid var(--border-dark);
}

.btn-secondary:hover {
  background-color: var(--background-dark-lighter);
  border-color: var(--primary);
}

.btn-danger {
  background: linear-gradient(135deg, var(--danger), #B71C1C);
  color: white;
  box-shadow: 0 4px 10px rgba(255, 23, 68, 0.3);
}

.btn-danger:hover {
  background: linear-gradient(135deg, #FF5252, var(--danger));
  box-shadow: 0 6px 15px rgba(255, 23, 68, 0.4);
  transform: translateY(-2px);
}

.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}

.btn-lg {
  padding: 1rem 2rem;
  font-size: 1.1rem;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 2.5rem;
  gap: 1rem;
}

/* Admin Panel */
.admin-container {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 2rem;
}

.admin-sidebar {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.8rem;
  box-shadow: var(--shadow-soft);
  height: max-content;
  border: 1px solid var(--border-dark);
}

.admin-menu {
  list-style: none;
}

.admin-menu li {
  margin-bottom: 0.8rem;
}

.admin-menu a {
  display: block;
  padding: 0.8rem 1.2rem;
  border-radius: var(--border-radius-sm);
  color: var(--text-light);
  transition: all var(--transition-speed);
}

.admin-menu a:hover, .admin-menu a.active {
  background-color: var(--background-dark-lighter);
  color: var(--primary);
}

.admin-content {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2rem;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border-dark);
}

/* Tables */
table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin-top: 1.5rem;
  border-radius: var(--border-radius-sm);
  overflow: hidden;
}

th, td {
  padding: 1.2rem;
  text-align: left;
}

th {
  font-weight: 600;
  background-color: var(--background-dark-lighter);
  color: var(--text-light);
}

td {
  border-bottom: 1px solid var(--border-dark);
}

tbody tr {
  transition: background-color var(--transition-speed);
}

tbody tr:hover {
  background-color: rgba(124, 77, 255, 0.05);
}

/* Messages */
.error-message {
  background-color: rgba(255, 23, 68, 0.15);
  color: #FF8A80;
  padding: 1.2rem;
  border-radius: var(--border-radius-sm);
  margin-bottom: 1.8rem;
  border-left: 4px solid var(--danger);
}

.success-message {
  background-color: rgba(0, 200, 83, 0.15);
  color: #69F0AE;
  padding: 1.2rem;
  border-radius: var(--border-radius-sm);
  margin-bottom: 1.8rem;
  border-left: 4px solid var(--success);
}

/* Footer */
footer {
  background-color: var(--background-dark-light);
  padding: 3rem 0 2rem;
  margin-top: 3rem;
  border-top: 1px solid var(--border-dark);
}

.footer-content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 2rem;
}

.footer-logo {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
}

.footer-description {
  max-width: 350px;
  color: var(--text-light-muted);
  margin-bottom: 1.5rem;
}

.footer-section {
  margin-bottom: 1.5rem;
}

.footer-section h3 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.footer-links {
  display: flex;
  flex-direction: column;
  list-style: none;
}

.footer-links li {
  margin-bottom: 0.8rem;
}

.footer-links a {
  color: var(--text-light-muted);
  transition: color var(--transition-speed);
}

.footer-links a:hover {
  color: var(--primary);
}

.footer-bottom {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--border-dark);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.footer-copyright {
  color: var(--text-light-muted);
  font-size: 0.9rem;
}

.social-links {
  display: flex;
  gap: 1rem;
}

.social-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: var(--background-dark-lighter);
  color: var(--text-light);
  transition: all var(--transition-speed);
}

.social-links a:hover {
  background-color: var(--primary);
  color: white;
  transform: translateY(-3px);
}

/* Responsive */
@media (max-width: 768px) {
  .post-grid {
    grid-template-columns: 1fr;
  }

  .admin-container {
    grid-template-columns: 1fr;
  }

  .navbar {
    flex-direction: column;
  }

  .brand {
    margin-bottom: 1rem;
  }

  .nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }

  .nav-links li {
    margin: 0.5rem;
  }

  .form-actions {
    flex-direction: column;
  }

  .form-actions button {
    width: 100%;
  }

  .post-title {
    font-size: 2rem;
  }

  .post-detail {
    padding: 1.5rem;
  }

  .footer-content {
    flex-direction: column;
  }
}

/* Image Preview */
.image-preview {
  margin-top: 1rem;
  border-radius: var(--border-radius-sm);
  overflow: hidden;
  border: 1px solid var(--border-dark);
}

.image-preview img {
  max-width: 100%;
  max-height: 250px;
  display: block;
}

/* Ratings */
.rating-container {
  display: flex;
  align-items: center;
  margin: 1.5rem 0;
}

.rating-label {
  margin-right: 1rem;
  font-weight: 500;
}

.stars {
  display: flex;
  gap: 0.3rem;
}

.star {
  font-size: 1.8rem;
  cursor: pointer;
  color: var(--text-light-muted);
  transition: all 0.2s ease;
}

.star:hover {
  transform: scale(1.2);
}

.star.filled {
  color: #FFD700;
}

/* Not Found */
.not-found-container {
  text-align: center;
  padding: 5rem 2rem;
  max-width: 700px;
  margin: 0 auto;
}

.not-found-container h1 {
  font-size: 8rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
}

.not-found-container h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.not-found-container p {
  margin-bottom: 2.5rem;
  color: var(--text-light-muted);
  font-size: 1.1rem;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.5s ease forwards;
}

/* Loading Spinner */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(124, 77, 255, 0.3);
  border-radius: 50%;
  border-top-color: var(--primary);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
